			//规格弹窗开关
			toggleSpec() {
				if (!this.productDetail.id) return;
				if(this.specClass === 'show'){
					if (!this.token) {
						this.specClass = 'none';
	          uni.showModal({
	             content: '会话已过期，是否跳转登录页面？',
	             success: (confirmRes) => {
	                 if (confirmRes.confirm) {
	                    uni.clearStorage();
	                    uni.reLaunch({
	                        url: '/pages/public/login'
	                    });
	                 }
	             }
	           });
